.side-nav {
    height: 100%;
}

.nav-tabs .nav-item {
    width: 100%;
    border: 1px solid #dddddd;
    cursor: pointer;
    position: relative;
}

.nav-tabs .nav-item::after {
    content: '>';
    color: blue;
    position: absolute;
    top: 5px;
    right: 3px;
    font-weight: bold;
}

.nav-tabs .nav-item.active {
    background-color: blue;
    color: #ffffff;
    border-color: blue;
}

.nav-tabs .nav-item.active::after {
    color: #ffffff;
}

.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:visited,
.nav-tabs .nav-link:active {
    border-color: transparent;
}

.help-section {
    position: fixed;
    top: 50%;
    right: 0;
    background-color: yellow;
    padding: 5px 5px 5px 35px;
    border-radius: 5px 0 0 5px;
}

.help-identifier {
    transform: rotate(-90deg);
    position: absolute;
    left: -15px;
    top: 33px;
}

.help-identifier::before {
    content: '<';
    position: absolute;
    left: 50%;
    transform: translate(-50%) rotate(90deg);
    top: -15px;
}

.help-sub-section {
    border: 1px solid #333333;
    margin: 5px 0;
}

.help-sub-section img {
    width: 30px;
    height: 30px;
}

@media screen and (max-width: 767px) {
    .nav-tabs .nav-item {
        width: 50%;
        text-align: center;
    }
    .nav-tabs .nav-item::after {
        display: none;
    }
}