@media screen and (max-width: 992px) {
    .container.app-body {
        max-width: 950px;
    }
}
@media screen and (max-width: 767px) {
    .container.app-body {
        max-width: 720px;
    }
}
@media screen and (max-width: 576px) {
    .container-fluid {
        padding: 0;
    }
    .container.app-body {
        max-width: 540px;;
    }
}