body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Common */
.form-submit-button {
  margin: 15px 0 0;
}

@media screen and (max-width: 992px) {
    .container.app-body {
        max-width: 950px;
    }
}
@media screen and (max-width: 767px) {
    .container.app-body {
        max-width: 720px;
    }
}
@media screen and (max-width: 576px) {
    .container-fluid {
        padding: 0;
    }
    .container.app-body {
        max-width: 540px;;
    }
}


.side-nav {
    height: 100%;
}

.nav-tabs .nav-item {
    width: 100%;
    border: 1px solid #dddddd;
    cursor: pointer;
    position: relative;
}

.nav-tabs .nav-item::after {
    content: '>';
    color: blue;
    position: absolute;
    top: 5px;
    right: 3px;
    font-weight: bold;
}

.nav-tabs .nav-item.active {
    background-color: blue;
    color: #ffffff;
    border-color: blue;
}

.nav-tabs .nav-item.active::after {
    color: #ffffff;
}

.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:visited,
.nav-tabs .nav-link:active {
    border-color: transparent;
}

.help-section {
    position: fixed;
    top: 50%;
    right: 0;
    background-color: yellow;
    padding: 5px 5px 5px 35px;
    border-radius: 5px 0 0 5px;
}

.help-identifier {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    position: absolute;
    left: -15px;
    top: 33px;
}

.help-identifier::before {
    content: '<';
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%) rotate(90deg);
            transform: translate(-50%) rotate(90deg);
    top: -15px;
}

.help-sub-section {
    border: 1px solid #333333;
    margin: 5px 0;
}

.help-sub-section img {
    width: 30px;
    height: 30px;
}

@media screen and (max-width: 767px) {
    .nav-tabs .nav-item {
        width: 50%;
        text-align: center;
    }
    .nav-tabs .nav-item::after {
        display: none;
    }
}
.RichEditor-root {
    background: #fff;
    border: 1px solid #ddd;
    font-family: 'Georgia', serif;
    font-size: 14px;
    padding: 15px;
  }
  
  .RichEditor-editor {
    border-top: 1px solid #ddd;
    cursor: text;
    font-size: 16px;
    margin-top: 10px;
  }
  
  .RichEditor-editor .public-DraftEditorPlaceholder-root,
  .RichEditor-editor .public-DraftEditor-content {
    margin: 0 -15px -15px;
    padding: 15px;
  }
  
  .RichEditor-editor .public-DraftEditor-content {
    min-height: 200px;
  }
  
  .RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
    display: none;
  }
  
  .RichEditor-editor .RichEditor-blockquote {
    border-left: 5px solid #eee;
    color: #666;
    font-family: 'Hoefler Text', 'Georgia', serif;
    font-style: italic;
    margin: 16px 0;
    padding: 10px 20px;
  }
  
  .RichEditor-editor .public-DraftStyleDefault-pre {
    background-color: rgba(0, 0, 0, 0.05);
    font-family: 'Inconsolata', 'Menlo', 'Consolas', monospace;
    font-size: 16px;
    padding: 20px;
  }
  
  .RichEditor-controls {
    font-family: 'Helvetica', sans-serif;
    font-size: 14px;
    margin-bottom: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .RichEditor-styleButton {
    color: #999;
    cursor: pointer;
    margin-right: 16px;
    padding: 2px 0;
    display: inline-block;
  }
  
  .RichEditor-activeButton {
    color: #5890ff;
  }

  button + button {
    margin: 0 10px 0 0;
  }

  @media screen and (max-width: 767px) {
    .RichEditor-root {
        margin: 10px 0;
    }
}
.story-block {
    text-align: center;
    padding: 0;
    margin: 0 0 75px 0;
}

.story-block h4 {
    text-align: center;
    text-decoration: underline;
}

.story-block .story-lines {
    text-align: left;
    max-height: 240px;
    overflow: hidden;
}

.story-block .read-more-button {
    margin: 10px 0 0;
}

.story-image-container {
    margin: 0 10px 0 0;
    float: left;
}

.story-image-container img {
    height: 140px;
    width: 140px;
}

.user-story-container {
    position: relative;
    top: 50px;
    border: 1px solid #333;
    padding: 15px;
}

.user-image {
    width: 100px;
    height: 100px;
    border: 1px solid #333333;
    border-radius: 50%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: -65px;
}

.story-description {
    margin: -50px 0 0;
}

.user-image img {
    height: 98px;
    width: 98px;
    border-radius: 50%;
}

@media screen and (max-width: 767px) {
    .story-block {
        margin: 15px 0 70px;
    }
    .story-block h4 {
        margin: 0 0 20px 0;
        font-size: 20px;
    }
}

.header {
    min-height: 100px;
    padding: 0 0 5px;
    margin: 0 15px 15px;
    border-bottom: 1px solid #333333;
}

.header img {
    height: 100px;
    margin: -12px 0 0 -20px;
}

.registration-links {
    float: right;
}

.registration-links span {
    margin: 0 0 0 10px;
    position: relative;
    top: 65px;
    color: blue;
    cursor: pointer;
}

.registration-links span img {
    width: 20px;
    height: 20px;
    margin: -2px 5px 0;
}

@media screen and (max-width: 576px) {
    .header {
        min-height: 80px;
    }
    .header img {
        height: 80px;
        margin: -10px 0 0 -20px;
    }
    .registration-links span {
        top: 40px;
        font-size: 12px;
    }
}
