.story-block {
    text-align: center;
    padding: 0;
    margin: 0 0 75px 0;
}

.story-block h4 {
    text-align: center;
    text-decoration: underline;
}

.story-block .story-lines {
    text-align: left;
    max-height: 240px;
    overflow: hidden;
}

.story-block .read-more-button {
    margin: 10px 0 0;
}

.story-image-container {
    margin: 0 10px 0 0;
    float: left;
}

.story-image-container img {
    height: 140px;
    width: 140px;
}

.user-story-container {
    position: relative;
    top: 50px;
    border: 1px solid #333;
    padding: 15px;
}

.user-image {
    width: 100px;
    height: 100px;
    border: 1px solid #333333;
    border-radius: 50%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    top: -65px;
}

.story-description {
    margin: -50px 0 0;
}

.user-image img {
    height: 98px;
    width: 98px;
    border-radius: 50%;
}

@media screen and (max-width: 767px) {
    .story-block {
        margin: 15px 0 70px;
    }
    .story-block h4 {
        margin: 0 0 20px 0;
        font-size: 20px;
    }
}